.portifolio {
    width: 100%;
}
.box-portifolio-escuro, .box-portifolio-claro {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    transition: 0.6s;
}

.box-portifolio-escuro {
    background-color: #000;
    transition: 0.6s;
}
.box-portifolio-claro {
    background-image: linear-gradient(to bottom, #ffffffaf, #51cccf);
    
    transition: 0.6s;
}
.box-portifolio-claro .descricao p {
    color: #000e28;
}
.box-portifolio-escuro .descricao h3 {
    color: #58efce;
} 
.box-portifolio-escuro .descricao p {
    color: #fff;
} 
.card {
    width: 100%;
    max-width: 780px;
    margin-top: 100px;
    margin-bottom: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.card:nth-child(even) {
    flex-direction: row-reverse;
}
.descricao h3 {
    font-size: 22px;
    letter-spacing: 2px;
    margin: 20px 5px;
    
}
.descricao {
    margin: 0;
    padding: 20px;
}
.box2 {
    text-align: right;
}
.descricao p{
    letter-spacing: 1px;
    width: 100%;
    max-width: 280px;
    margin: 20px 10px;
    font-weight: 600;
}
.descricao a {
    width: 115px;
    padding: 5px 10px;
    border-radius: 5px;
    margin: 20px;
    text-decoration: none;
    color: #000000;
    font-weight: 900;
    background-color: transparent;
    border: 3px solid #000;
    display: flex;
    align-items: center;
    justify-content: center;
}
.box-portifolio-escuro .descricao a {
    color: #fff;
    border-color: #51cccf;
}

.card img {
    width: 100%;
    max-width: 370px;
    border-radius: 2px;
    transition: 0.3s;
}
.card img:hover {
    box-shadow: 0 0 10px 1px #164b4c;
    background-color: #fffffff3;
}
.box-portifolio-escuro .card img:hover {
    box-shadow: 0 0 10px 1px #58efce;
}
.card-react img {
    width: 150px;
    margin: 10px 150px;
}
.card-react {
    justify-content: space-between;
    height: 380px;
}

.descricao img {
    width: 35px;
    height: 35px;
    margin: 2px;
}

.descricao a img {
    width: 20px;
    height: 20px;
}
.descricao a p {
    margin: 0 5px;
}
.descricao a img:hover {
    box-shadow: none;
    background-color: transparent;
}

.box-portifolio-escuro .descricao a img {
    filter: invert(100%);
}

@media (max-width: 426px) {
    .box-portifolio-claro .descricao p {
        color: #000e28;
        margin: 10px auto;
    }
    .descricao a {
        margin: 30px auto;
    }
    .descricao h3 {
        margin: 20px 0;
    }    
    .card:nth-child(even) {
        flex-direction: column;
    }
    .card {
     flex-direction: column;
     justify-content: center;
     margin: 100px auto;
    }
    .card-react {
     margin: 200px 0;
    }
    .descricao {
     text-align: center;
    }
    .descricao p {
        margin: 10px 0;
    }
}

@media (min-width: 427px) and (max-width: 780px) {
       
       .card {
        flex-direction: column;
        justify-content: center;
        margin: 150px auto;
       }
       .descricao a {
        margin: 30px auto;
        }
       .card:nth-child(even) {
        flex-direction: column;
        }
       .descricao {
        text-align: center;
       }
}

