.topo-escuro, .topo-claro {
    width: 100%;
    height: 60px;
    transition: 0.6s;
    display: flex;
    position: fixed;
    z-index: 1;
}
.topo-claro {
    background-color: #f5f5f5;
}
.topo-escuro {
    background-color: #000000;
}
.topo-escuro .navegacao a {
    color: #fff;
    transition: 0.3s;
}
.topo-escuro .navegacao a:hover {
    color: #58efce;
    transition: 0.3s;
}
.topo-escuro .botao img {
    filter: invert(100%);
    width: 50px;
    height: 50px;
}
.box-topo {
    width: 100%;
    max-width: 1024px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}

.box-topo img {
    width: 90px;
}

.botao {
    border: 0;
    background-color: #ffffff00;
}
.navegacao {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin-right: 10px;
}
.navegacao a {
    text-decoration: none;
    color: #000e28;
    margin: 0 20px;
    text-transform: uppercase;
    font-weight: bold;
}
.navegacao p {
    margin: 10px;
    font-weight: bold;
}
.topo-claro .navegacao p {
    color: #000e28;
}

@media (max-width: 426px) {
    .topo-escuro, .topo-claro {
        height: 105px;
    }
    .box-topo img {
        width: 30px;
        position: absolute;
        margin-top: 15px;

    }
    .navegacao {
        margin: 0;
        width: 100%;
        justify-content: center;
    }
    .botao {
        margin: 5px 45px;
    }
    .navegacao a {
        margin: 0 15px ;
    }
    .box-topo {
        align-items: center;
    }
    .logo {
        display: none;
    }
}

@media (min-width: 427px) and (max-width: 768px) {
    .box-topo img {
        width: 30px;
    }

    .box-topo {
        align-items: center;
    }
    .logo {
        display: none;
    }
}