.banner {
    width: 100%;
    height: 50vh;
}
.box-banner-escuro, .box-banner-claro {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    transition: 0.6s;
}

.box-banner-escuro {
    background-color: #000000;
    color: #58efce;
}
.box-banner-escuro p {
    color: #fff;
}
.box-banner-claro {
    background-color: #ffffff;
    color: #000e28;
}
.banner h1 {
    font-size: 84px;
    font-weight: 700;
    text-align: center;
    transition: 0.6s;
}
.banner p {
    margin-top: 20px;
    text-align: center;
    font-weight: bold;
    font-size: 28px;
    letter-spacing: 2px;
    transition: 0.6s;
}
.efeito-escrita {
    border-right: 3px solid #fff;
    white-space: nowrap;
    overflow: hidden;
    color: #000000;
}

.typing-animation {
    animation: blinkCursor 700ms steps(40) infinite normal, 
    typing 2.2s steps(40) 1s normal both;
}

@keyframes typing {
    from {
        width: 0;
    }
    to {
        width: 13em;
    }
}

@keyframes blinkCursor {
    from{
        border-right-color: rgba(255, 255, 255)
    }
    to {
        border-right-color: rgba(0, 0, 0)
    }
}

@media (max-width: 768px) {
    .box-banner-escuro, .box-banner-claro {
        padding: 50px 0 0 0;
        align-items: center;
        text-align: center;
    }
    .banner h1 {
        text-align: center;
        font-size: 36px;
    }
    .banner p {
        margin-top: 0;
        font-size: 26px;
    }
}

